@use 'general/variables' as v;
@use 'general/mixins' as m;

.places {

    &__grid {
        display: grid;
        grid-template-columns: repeat(4, 70vw);
        column-gap: 4rem;
        
        @include m.horizontalScrolling;

        @include m.tablet {
            @include m.grid(2, 2rem);
        }

        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }
}

.place {

    &__image {
        border-radius: 1rem;
    }

    &_name {
        font-size: 1.8rem;
    }
}