@use 'general/variables' as v;
@use 'general/mixins' as m;

.destinations {

    &__grid {
        @include m.grid(1, 2rem);

        @include m.tablet {
            @include m.grid(2, 2rem);
        }

        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }
}

.destination {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem;

    &__image {
        width: 14rem;
        height: 14rem;
    }

    &__name,
    &__distance {
        margin: 0;
    }
}