/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 2rem 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.header {
  background-image: url(../img/header.webp);
  background-size: cover;
  background-position: center;
  padding: 3rem 0 20rem 0;
}
.header__search {
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__search {
    margin-top: 30rem;
  }
}
.header__search-button {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #e8505b;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .header__search-button {
    display: inline-block;
  }
}
.header__search-button:hover {
  transform: translate(0, -1rem);
}

.bar {
  display: none;
}
@media (min-width: 768px) {
  .bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.bar__navigation {
  display: flex;
  gap: 4rem;
}
.bar__link {
  color: #FFFFFF;
}
.bar__link:hover {
  color: rgb(189, 30, 89);
}
.bar__title {
  color: #FFFFFF;
  margin: 0;
}

.form {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 3rem;
  box-shadow: 0px 3px 7px 0 #686868;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .form {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.form__group {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  border-radius: 3rem;
  transition: box-shadow 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .form__group:nth-child(1) {
    padding-left: 4rem;
  }
}
.form__group:not(:last-child):hover {
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.4);
}
.form__label {
  font-size: 1.2rem;
  font-weight: 900;
  text-align: center;
}
.form__input {
  border: none;
  width: 100%;
  color: #686868;
  text-align: center;
}
.form__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  background: rgb(189, 30, 89);
  padding: 0 2rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background 0.3s linear;
}
.form__submit:hover {
  background: #de3071;
}

.destinations__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .destinations__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .destinations__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.destination {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
}
.destination__image {
  width: 14rem;
  height: 14rem;
}
.destination__name, .destination__distance {
  margin: 0;
}

.places__grid {
  display: grid;
  grid-template-columns: repeat(4, 70vw);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.places__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .places__grid {
    overflow-x: auto;
  }
}
@media (min-width: 768px) {
  .places__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .places__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.place__image {
  border-radius: 1rem;
}
.place_name {
  font-size: 1.8rem;
}

.host {
  color: #FFFFFF;
  padding: 5rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 15rem;
  background-image: linear-gradient(to bottom, #000000 0%, #000000 50%, transparent 50%), url(../img/anfitrion.jpg);
  background-size: 100%, 60rem;
  background-position: center, center bottom;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .host {
    background-size: 100%, 70rem;
    background-position: center, center 200%;
  }
}
@media (min-width: 768px) {
  .host {
    grid-template-rows: unset;
    background-image: linear-gradient(to right, #000000 0%, #000000 30%, transparent 70%), url(../img/anfitrion.jpg);
    background-size: 100%, auto;
    background-position: center, 50% 30%;
    padding: 15rem 5rem;
  }
}
@media (min-width: 1024px) {
  .host {
    background-position: center, right center;
  }
}
@media (min-width: 768px) {
  .host__content {
    max-width: 50%;
  }
}
.host__text {
  color: #FFFFFF;
}
.host__link {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #000000;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  margin-top: 1rem;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .host__link {
    display: inline-block;
  }
}
.host__link:hover {
  transform: translate(0, -1rem);
}

.experiences__list {
  display: grid;
  grid-template-columns: repeat(3, 70vw);
  column-gap: 2rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiences__list > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiences__list {
    overflow-x: auto;
  }
}
@media (min-width: 768px) {
  .experiences__list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.experience__image {
  border-radius: 1rem;
}
.experience__title {
  font-weight: 700;
  font-size: 2rem;
}
.experience__description {
  font-size: 1.8rem;
}

.footer {
  background-color: #f3f3f3;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__heading {
  text-transform: uppercase;
  font-size: 1.3rem;
  text-align: center;
}
.footer__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__link {
  color: #686868;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.footer__link:hover {
  color: #e8505b;
}
.footer__link--bar {
  margin-bottom: 0;
}
.footer__link--bar::before {
  content: " # ";
  color: rgb(189, 30, 89);
}
@media (min-width: 1024px) {
  .footer__link--bar:first-child {
    margin-left: 10rem;
  }
}
.footer__bar {
  border-top: 1px solid #cecece;
  margin-top: 2rem;
  padding: 1.5rem 0;
}
.footer__navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 1024px) {
  .footer__navbar {
    justify-content: flex-start;
    gap: 4rem;
  }
}